.our-services{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-bottom: 100px;
    margin-top: 23px;
}
.our-services h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;
}
.our-services hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}
.our-services p{
    color:#252525;
    font-size: 20px;
    font-weight: 400;
}
.services{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 50px;
    gap: 80px;
}
