.footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;
    background: rgb(0, 0, 0);
    color: white;

}
.footer-logo{
    display: flex;
    align-items: center;
    gap: 20px;
}
.footer-logo img{
    width: 100px;
    
}
.footer-logo p{
    color: #383838;
    font-size: 46px;
    font-weight: 700;
    color: white;
}
.footer-links{
    display: flex;
    list-style: none;
    gap: 50px;
    color: white;
    font-size: 20px;
}
.footer-links li{
    cursor: pointer;
}
.footer-socials{
    display: flex;
    gap: 20px;
}
.icons-container{
    padding: 10px;
    padding-bottom: 6px;
    background: #fbfbfb;
    border: 3px solid #ebebeb;
}
.copyright{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    width: 100%;
    margin-bottom: 30px;
    color: white;
    font-size: 20px;
}
.copyright hr{
    width: 80%;
    border: none;
    border-radius: 10px;
    height: 3px;
    background: #c7c7c7;
}