.hero{
    margin-top: 30px;
    position: relative;
    text-align: center;
    height: 100;
    align-items: center;
    justify-content: center;
    display: flex;
}
.hero img{
    height: 90vh;
    width: 80%;
    border-radius: 50px;
    align-items: center;
}
.text{
    position: absolute;
    font-size: 80px;
    color: white;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.text1{
    position: absolute;
    font-size: 50px;
    color: white;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.hero-latest-btn{
    position: absolute;
    top: 80%;
    left: 50%;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background: black;
    color: white;
    font-size: 22px;
    transform: translate(-50%, -50%);
    font-weight: 500;
}
